@import "var.scss";
.icon_play {
	position: absolute;
	bottom: 20px; left: 20px;
}



/*****************************************/


.featured_left {
	width: 752px; max-width: 100%;
	.embed_video_new {
		padding-bottom: 0 !important;
		.box_embed_video {
			margin-bottom: 0;
		}
		.icon_blockvideo {
		    transform: translate(0, 0);
	        left: 20px;
		    bottom: 20px;
		    top: auto;
		}

		.img_icon {
		    display: none;
		}
	}
	.embed-container {
	  iframe {
	    position: absolute;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	  }
	  position: relative;
	  padding-bottom: 56.25%;
	  padding-top: 0;
	  margin-bottom: 0;
	}
}
.featured_right {
	background: #f7f7f7;
	padding: 30px 0px 30px 20px;
    width: calc( 100% - 752px ); max-width: 100%;
    @media (max-width: 991px) {
    	width: 100%;
    }
    > .scroll-content {
    	max-height: 362px;
    	padding-right: 20px !important;
    }

	.title {
		font: 700 px_to_rem(24px) $fontmain;
		margin-bottom: 10px;	
	}
	.lead {
		font-size: 16px; margin-bottom: 10px;
	}
	.block_share {
		margin-bottom: 10px;
	}
	.filter_coment {
		padding-right: 0;
		select {
			font-size: px_to_rem(14px);
			background: transparent;
			border: none;
			option {
				
			}
		}
	}
	.ykien_vne .left {
		font-size: px_to_rem(18px); 
	}
}

.category_video {
	position: relative;
	.title {
		font: 700 px_to_rem(24px) $fontmain;
		margin-bottom: 15px;
	}
	.grid {
		margin-bottom: 28px;
		.art_item {
			& + .button_xemthem {
				
				margin-bottom: -28px;
			}
		}
	}
	.art_item {
		&:before {
			display: none;
		}
		.thumb_art .icon_thumb {
			width: 32px; height: 32px;
			background: transparent;	
		    left: 5px;
   			bottom: 5px;		
		}
		.content {
			.title_news {
				font-weight: 500;
				font-size: px_to_rem(18px);
			}
		}
	}
	&:last-of-type {
		.line_1px {
			display: none;
		}
		.grid {
			margin-bottom: 0;
		}
		.art_item {
		}
	}
}



.video--see_more {
	font: 400 px_to_rem(16px) 'Arial';
	color: #999;
	position: absolute;
	right: 0; top: 7px;
}

.cat_time {
	margin-bottom: 10px;
	.cat {
		font-weight: 700; color: $colormain; margin-right: 10px;
	}
	.spanDateTime {
		color: #999;
	}
}


#video_noibat {
	margin-bottom: 0;
}

.title-style1 {
	position: relative; text-align: center;
	margin-bottom: 25px;
	&:before {
		content: "";
		height: 1px; width: 100%;
		background: #eee;
		position: absolute;
		top: 10px;
		left: 0; right: 0;
	}
	.cat--see_more {
		display: inline-block;
		padding: 0 20px;
		background: #fff;
		margin: 0 auto;
		position: relative;
		font-size: 16px;
		color: #999;
	}
}

.button_xemthem {
	text-align: center;
	margin-left: 28px;
	margin-top: 28px;
}

.box_sotap {
	border-bottom: 1px solid #e2e2e3;
    margin-bottom: 10px;
    padding-bottom: 10px;
	.label {
	    float: left;
	    margin-top: 7px;
	    margin-right: 10px;
	}
	ul {
		float: left;
		li {
			float: left;
			width: 32px; height: 32px;
			border-radius: 50%;
			margin: 0px 0 0 5px;
			position: relative;
			text-align: center;
			line-height: 32px;
			&:first-of-type {
				margin-left: 0;
			}
			&:hover, &.active {
				background: #ededed;
				a {
					font-weight: 700;
					color: $colormain;

				}
			}
		}
	}
}