@mixin cubic-transition($delay, $duration, $property) {
    transition: {
        duration: $duration;
        property: $property;
        timing-function: cubic-bezier(.7,1,.7,1);
    }
}
@mixin clearfix {
  clear: both;
  &::before, &::after {
     display: table;
     content:"";
     clear: both;
  }
}


